body {
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.box-container {
  margin: 10px 0;
}

.box {
  width: 300px;
  margin: 10px;
  padding: 10px;
  line-height: 25px;
  float: left;
  border-radius: 4px;
  border: 1px solid #999;
}

div[class$="true"] {
  width: 300px;
  margin: 10px;
  padding: 10px;
  line-height: 35px;
  float: left;
  border-radius: 5px;
  border: 1px solid #999;
  background-color: rgb(207, 202, 207);
}

div[class$="false"] {
  width: 300px;
  margin: 10px;
  padding: 10px;
  line-height: 35px;
  float: left;
  border-radius: 5px;
  border: 1px solid #999;
  background-color: white;

}

img {
  width: 45px;
  height: 45px;
}

.clearboth {
  clear: both;
}


b {
  font-size: medium;
  font-family: "Courier New";
  font: bold;
  font: 1em sans-serif;
  margin-left: 2em;
}

.avatar {
  vertical-align: middle;
  width: 55px;
  height: 55px;
  border-radius: 50%;
}

th {
  text-align: left;
  font-size: medium;
  font: 1em sans-serif;
}

td {
  margin-left: 15px;
  text-align: right;
  font: 1em sans-serif;
}

span[class$="safe"] {
  display: none;
}

button[class$="true"] {
  display: none;
}


.btn-smlr {
  padding: 0px 2px;
  margin-bottom: 0px;
  margin-top: 0px;
}
